@import '~assets/styles/h3Imports';

.root {
  @include header-dropdown;

  .dropdown {
    [data-option-value='small'] {
      font-size: rem(16);
      font-weight: 600;
      line-height: line-height(24, 16);
    }

    [data-option-value='medium'] {
      font-size: rem(19);
      font-weight: 700;
      line-height: line-height(24, 19);
    }

    [data-option-value='large'] {
      font-size: rem(27);
      font-weight: 700;
      line-height: line-height(32, 27);
    }

    [data-fontsize='AAA'] & {
      width: max-content;
    }
  }
}

@media #{$small-down} {
  .root {
    font-size: 0;
  }
}
