@import '~assets/styles/h3Imports';

.root {
  --schedule-banner-background-color: var(--background-2, #c2e8ff);
  --schedule-banner-color: var(--gray-5, #0a1414);

  background-color: var(--schedule-banner-background-color);
  border-radius: var(--schedule-banner-border-radius, 16px);
  color: var(--schedule-banner-color);
  display: flex;
  margin-top: var(--spacing-s);
  max-width: var(--schedule-banner-max-width);
  min-height: var(--schedule-banner-min-height, 304px);
  overflow: hidden;
}

.banner-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 44px 56px;
  width: 50%;

  --button-min-width: none;

  .banner-title {
    @include h1;

    margin: 0;
  }

  .banner-description {
    @include paragraph;

    margin-bottom: em(32);
  }
}

.illustration-mobile {
  display: none;
}

.illustration {
  align-items: center;
  display: flex;
  justify-content: center;
  width: 50%;

  img {
    height: 100%;
    margin-top: auto;
    min-height: em(304);
    object-fit: cover;
    width: 100%;
  }
}

.buttons {
  display: flex;
  flex-wrap: wrap;
  gap: em(16);
  justify-content: center;
  width: 100%;

  button {
    min-width: em(183, 14);
  }
}

@media (max-width: $max-tablet-size) {
  .banner-content {
    padding: 34px 32px;

    .banner-description {
      margin-bottom: em(24);
    }
  }

  .buttons {
    flex-direction: column;

    button {
      min-width: em(156, 14);
      width: 100%;
    }
  }
}

@media (max-width: $max-phone-size) {
  .root {
    flex-direction: column-reverse;
  }

  .illustration {
    width: 100%;

    img {
      min-height: em(144);
      object-fit: contain;
    }
  }

  .banner-content {
    padding: 16px;
    width: 100%;

    .banner-description {
      margin-bottom: em(16);
      margin-top: em(8);
    }

    .illustration-mobile {
      display: none;
    }
  }
}
